<template>
  <div>
    <b-table hover :fields="fields" :items=" this.$store.state.cart">
      <template v-slot:cell(item)="data">{{ data.index + 1 }}</template>
    </b-table>
    <b-button pill v-b-modal.modal-1 variant="success">Order Now</b-button>
    <b-modal id="modal-1" title="BootstrapVue" hide-footer="">
      <p class="my-4">Payment Successful! Your Order has been placed successfully</p>
       <b-button class="mt-2" variant="outline-primary"
       @click="putOrder" pill block to="/orders">OK</b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fields: ['item', 'name', 'price', 'gst'],
      cart: [],
    };
  },
  mounted() {
    this.cart = this.$store.state.cart;
    for (let i = 0; i < this.cart.length; i += 1) {
      this.cart[i].quantity = 0;
    }
  },
  methods: {
    increaseQuantity(index) {
      this.$store.state.cart[index].quantity += 1;
    },
    decreaseQuantity(index) {
      this.$store.state.cart[index].quantity -= 1;
    },
    putOrder() {
      this.$store.state.orders.push(this.$store.state.cart);
      this.$store.state.cart = [];
    },
  },
};
</script>
